<template>
  <div ref="chart_ref" class="chart_area"></div>
</template>

<script>
export default {
  data() {
    return {
      chart:null,
      allData :null,
    }
  },
  mounted(){
    this.initChart()
    this.getData()
    this.updateSize()
    this.updateChart()
    window.addEventListener('resize',this.updateSize)
    document.addEventListener('leftChange',this.updateSize)
  },
  methods:{
    initChart(){
      this.chart = this.$echarts.init(this.$refs.chart_ref,'walden')
      const option = {
        title:{
          text: '主要品牌店铺数量',
        },
        tooltip:{
          trigger:'axis',
          axisPointer:{
            z:0,
            type:'line',
            lineStyle:{
              color:'#2d3443'
            }
          }
        },
        xAxis:{
          type:'category',
        },
        yAxis:{
          type:'value',
          data:[]
        },
        series:{
          type:'bar',
          // 柱子
          itemStyle:{
            // 柱子边缘
            barBorderRadius : [16,16,0,0],  //左上 右上
            // 颜色渐变
            color: new this.$echarts.graphic.LinearGradient(0,1,0,0,[
              {
                offset:0,   //百分之0
                color:'#5052ee'
              },
              {
                offset:1,  //百分之百
                color:'#ab6ee5'
              }
            ])
          },
          // 数字提示
          label:{
            show : true,
            position: 'top',
            textStyle:{
              color: 'white'
            }
          }
        }
      }
      this.chart.setOption(option)
    },
    async getData(){
      // await this.$http.get('').then(
      //   response => {console.log(response.data)},
      //   error => {console.log(error)}
      // )

      // response = await this.$http.get('')
      this.allData = [
        {
          name:'lala',
          value:15
        },
        {
          name:'你爹',
          value:30
        },
        {
          name:'尼玛',
          value:50
        },
        {
          name:'尼玛',
          value:50
        },
        {
          name:'尼玛',
          value:50
        }
      ]
    },
    updateChart(){
      const sellerNames = this.allData.map((item)=>{
        return item.name
      })
      const sellerValues = this.allData.map((item)=>{
        return item.value
      })
      const option = {
        xAxis:{
          data: sellerNames
        },
        series:{
          data: sellerValues,
        },
      }      
      this.chart.setOption(option)
    },
    updateSize(){
      console.log(this.$refs.chart_ref.offsetWidth )
      const titleFontSize = this.$refs.chart_ref.offsetWidth / 100 *3.6
      const option = {
        title:{
          text: '主要品牌店铺数量',
          textStyle: {
            fontSize : titleFontSize
          }
        },
        tooltip:{
          axisPointer:{
            lineStyle:{
              width: titleFontSize,
            }
          }
        },
        series:{
          // 柱子
          barWidth: titleFontSize,
          itemStyle:{
            // 柱子边缘
            barBorderRadius : [titleFontSize/2,titleFontSize/2,0,0],  //左上 右上
          },
          // 数字提示
        }
      }
      this.chart.setOption(option)
      this.chart.resize()
    }
  }
}
</script>

<style>
.chart_area{
  width: 100%;
}
</style>